import { CSSProperties } from "react";
import { Link } from "react-router-dom";

import "./styles.scss";

type Variant = "text" | "contained" | "outlined";

type ButtonProps = {
  to?: string;
  full?: boolean;
  size?: "small" | "medium" | "large";
  style?: CSSProperties;
  onClick?: () => void;
  children: React.ReactNode;
  startIcon?: React.ReactNode;
  variant?: Variant;
};

const Button: React.FC<ButtonProps> = ({
  to,
  full,
  size = "medium",
  style,
  onClick,
  children,
  startIcon,
  variant = "contained",
}) => {
  if (to) {
    return (
      <Link
        to={to}
        className={`button ${full ? "button_full" : ""} ${
          size ? "button_" + size : ""
        } button_${variant}`}
        style={style}
        onClick={onClick}
      >
        {startIcon && <span className="button__icon">{startIcon}</span>}
        {children}
      </Link>
    );
  }

  return (
    <button
      type="button"
      className={`button ${full ? "button_full" : ""} ${
        size ? "button_" + size : ""
      } button_${variant}`}
      style={style}
      onClick={onClick}
    >
      {startIcon && <span className="button__icon">{startIcon}</span>}
      {children}
    </button>
  );
};

export default Button;
