import { JSX } from "react/jsx-runtime";
import { Link } from "react-router-dom";

import Title from "components/Title";
import Header from "components/Header";
import Crumbs from "components/Crumbs";
import Typography from "components/Typography";
import Footer from "components/Footer";
import Card from "components/Card";
import Button from "components/Button";
import Pagination from "components/Pagination";
import Sorting from "components/Sorting";

import { ReactComponent as ArrowIcon } from "assets/icons/arrow.svg";
import { ReactComponent as FilterIcon } from "assets/icons/filter-icon.svg";

import { paths } from "constants/paths";
import { productsData } from "../data/products";
import useCatalogIdPage from "./useCatalogIdPage";

import "./styles.scss";

const CatalogIdPage = () => {
  const { navigate, title, sortingIsOpen, setSortingIsOpen } =
    useCatalogIdPage();

  return (
    <>
      <div className="wrapper">
        <Title>{title}</Title>

        <Header />

        <div className="catalog-id">
          <Crumbs path={title} />
          <Typography variant="h3">{title}</Typography>

          <div className="catalog-id__filter">
            <button
              className="catalog-id__filter__button"
              onClick={() => {
                setSortingIsOpen(true);
              }}
            >
              <Typography
                variant="subtitle2"
                className="catalog-id__filter__button__text"
              >
                По наименованию
              </Typography>
              <ArrowIcon
                className={
                  "catalog-id__filter__button__icon catalog-id__filter__button__icon_arrow " +
                  (!!sortingIsOpen
                    ? "catalog-id__filter__button__icon_arrow-open"
                    : "")
                }
              />
            </button>
            <button
              className="catalog-id__filter__button"
              onClick={() => {
                navigate(paths.filters);
              }}
            >
              <FilterIcon className="catalog-id__filter__button__icon catalog-id__filter__button__icon_filter" />
              <Typography
                variant="subtitle2"
                className="catalog-id__filter__button__text"
              >
                Фильтры
              </Typography>
            </button>
          </div>

          <Typography className="catalog-id__result" variant="body1">
            Найдено 198 товаров
          </Typography>

          <div className="catalog-id__products">
            {productsData?.map(
              (
                product: JSX.IntrinsicAttributes & {
                  id: number;
                  title: string;
                  brand: string;
                  price: string;
                  image: string;
                }
              ) => (
                <Link
                  key={product.id}
                  to={`${paths.product}/${product.id}`}
                  state={{ product }}
                  className="catalog-id__product"
                >
                  <Card {...product} />
                </Link>
              )
            )}
          </div>
        </div>

        <Button
          full
          size="medium"
          variant="outlined"
          style={{ marginBottom: 16 }}
        >
          Показать еще
        </Button>

        <Pagination />
      </div>

      <Sorting isOpen={sortingIsOpen} setOpen={setSortingIsOpen} />

      <Footer />
    </>
  );
};

export default CatalogIdPage;
